import { useRouter } from "next/router";
import { useState } from "react";
import { useContext } from "react";
import { AuthContext } from "../context/AuthContext";
import { v4 as uuidv4 } from "uuid";
import axios from "axios";

const Home = () => {
  const { user } = useContext(AuthContext);
  const router = useRouter();
  const [meetingId, setMeetingId] = useState("");

  // Function to start a new meeting
  const startMeeting = async () => {
    if (!user) {
      router.push("/login");
      return;
    }

    const newMeetingId = uuidv4();
    await axios.post("http://localhost:8000/start-meeting", {
      meeting_id: newMeetingId,
      username: user,
    });

    router.push(`/meeting/${newMeetingId}`);
  };

  // Function to join an existing meeting
  const joinMeeting = () => {
    if (meetingId.trim()) {
      router.push(`/meeting/${meetingId}`);
    }
  };

  return (
    <div className="flex flex-col items-center justify-center h-screen bg-gray-100">
      <h1 className="text-3xl font-bold mb-6">Welcome to SuperSoul Video Meet</h1>

      <button 
        className="px-6 py-3 bg-blue-500 text-white rounded-lg hover:bg-blue-600 mb-4"
        onClick={startMeeting}
      >
        Start a New Meeting
      </button>

      <div className="flex space-x-2">
        <input
          type="text"
          placeholder="Enter Meeting ID"
          value={meetingId}
          onChange={(e) => setMeetingId(e.target.value)}
          className="p-2 border border-gray-300 rounded-md"
        />
        <button 
          className="px-4 py-2 bg-green-500 text-white rounded-md hover:bg-green-600"
          onClick={joinMeeting}
        >
          Join Meeting
        </button>
      </div>
    </div>
  );
};

export default Home;
